import React from 'react';
import { Link } from 'react-router-dom';

const Hint = {
  id: { label: "" },
  question_id: { label: " " },
  type: {
    label: "", options: [
      { key: "null", text: "", value: null },
      { key: "image", text: "", value: "image" },
      { key: "choice", text: "", value: "choice" },
      { key: "letter", text: "", value: "letter" },
      { key: "try", text: "", value: "try" },
      // { key: "time", text: "", value: "time" },
    ]
  },
  value: { label: "" },
  price: { label: "" },
};

export default Hint;

export const print = a => {
  a.question_id = <Link to={`/questions/${a.question_id}`}>{a.question_id}</Link>
  return a;
}
